<template>
  <div id="report" class="grey lighten-5">
    <v-container class="pt-5">
      <v-row id="top">
        <v-col>
          <v-card color="white">
            <div class="d-flex flex-no-wrap justify-space-between">
              <div>
                <v-card-title class="text-h5 red--text font-weight-black">{{ applicant.name }}</v-card-title>
                <v-card-subtitle>{{ applicant.phone_number }} |<span class="red--text">{{ applicant.email }}</span></v-card-subtitle>
              </div>
              <v-avatar class="ma-3" size="125" tile>
                <v-img src="https://source.unsplash.com/100x100/?portrait"></v-img>
              </v-avatar>
            </div>
          </v-card>
        </v-col>
        <v-col>
           <v-sparkline
            color="red"
            type="bar"
            show-labels
            auto-line-width
            :value="applicant.quiz_results"
          ></v-sparkline>
        </v-col>
      </v-row>
      <v-row>
        <h2 class="text-center">Reviews</h2>
        <v-timeline>
          <v-timeline-item v-for="(review, index) in applicant.reviews" :key="index" color="red" small>
            <!-- <template v-slot:opposite>
              <span>Tus eu perfecto</span>
            </template> -->
            <v-card class="elevation-2">
              <v-card-title class="text-h5"> {{ getProctorName(review.proctor_id) }} </v-card-title>
              <v-card-text>
               {{ review.coment }}
              </v-card-text>
              <v-card-text>
                <v-chip-group column>
                  <v-chip
                    color="error"
                    v-for="(strength, index) in review.strengths"
                    :key="index"
                  >
                    {{ strength }}
                  </v-chip>
                </v-chip-group>
              </v-card-text>
            </v-card>
          </v-timeline-item>
        </v-timeline>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import ApplicantDataService from "@/services/ApplicantDataService";
import ProctorDataService from "@/services/ProctorDataService";

export default {
  name: "report",
  data: () => ({
    applicant: null,
  }),
  created() {
    this.getApplicant();
  },
  methods: {
    getApplicant() {
      ApplicantDataService.get(this.$route.params.id)
        .then((res) => this.applicant = res.data)
        .catch((err) => console.error(err))
    },
    getProctorName(id) {
      ProctorDataService.get(id)
        .then((res) => { return res.data.name })
        .catch((err) => console.error(err))
    }
  },
};
</script>

<style lang="scss">
  #top{ 
    padding-top: 100px;
    position:relative;
  }
</style>